$blue: #1875e7;

h1 {
  color: $blue;
}

.main-menu {
  max-width: 640px;
  margin-left: auto;
  margin-right: auto;
}

ul.horizontal-menu
{
  list-style-type:none;
  margin:0;
  padding:0;
  overflow:hidden;
}

li.horizontal-left-menu {
  float:left;
}

li.horizontal-right-menu {
  float:right;
}

.horizontal-left-menu-content {
  display:block;
  min-width:120px;
}

.horizontal-right-menu-content {
  display:block;
  text-align: right;
  min-width:120px;
}

ul.vertical-menu
{
  list-style-type:none;
  margin:0;
  padding:0;
}

li.vertical-menu {

}

.vertical-menu-content {
  display:block;
  width:120px;
}

.content {
  max-width: 640px;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.error-message {
  color: #dd1144;
}

.success-message {
  color: #16dd3c;
}

.login-button {
  right: 100px;
}

.auto-break {
  word-break:break-all;
}

